// =============================================================================
// VERCEL CRON ENDPOINT: House x402 Traffic Job
// =============================================================================
// GET /api/cron/house-x402-job
// Generates steady, honest traffic through SMF by calling a real x402 endpoint
// Called by Vercel Cron every 10 minutes (schedule: "*/10 * * * *")
// ~144 calls/day, well under 200 calls/day limit

import { NextRequest } from 'next/server';
import { createWalletClient, http, type Address } from 'viem';
import { privateKeyToAccount } from 'viem/accounts';
import { base } from 'viem/chains';
import { createLogger } from '@/lib/logger';
import { withCronJobTracking, type CronJobContext } from '@/lib/cron-job-wrapper';

const logger = createLogger({ component: 'HouseX402Job' });

// =============================================================================
// CONFIGURATION
// =============================================================================

/**
 * House Job Configuration
 * Target: URL Enrich endpoint - x402-protected at 0.01 USDC on Base
 */
const HOUSE_JOB_CONFIG = {
  description: 'House traffic SMF test job',
  targetPriceUsdc: '0.01',
  priceAmount: '10000', // 0.01 USDC in 6-decimal format
  network: 'base',
  chainId: 8453,
  asset: 'USDC',
  payload: {
    url: 'https://nexflowapp.app',
  },
};

// =============================================================================
// EIP-712 SIGNING
// =============================================================================

// EIP-3009 domain for USDC on Base (per official @x402/evm SDK)
const EIP712_DOMAIN = {
  name: 'USD Coin',
  version: '2',
  chainId: HOUSE_JOB_CONFIG.chainId,
  verifyingContract: '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913' as Address, // Base USDC
};

// EIP-3009 TransferWithAuthorization types (per official x402 SDK)
const EIP712_TYPES = {
  TransferWithAuthorization: [
    { name: 'from', type: 'address' },
    { name: 'to', type: 'address' },
    { name: 'value', type: 'uint256' },
    { name: 'validAfter', type: 'uint256' },
    { name: 'validBefore', type: 'uint256' },
    { name: 'nonce', type: 'bytes32' },
  ],
};

function normalizePrivateKey(key: string): string {
  let normalized = key.trim().replace(/^["']|["']$/g, '').replace(/\s+/g, '');
  if (!normalized.startsWith('0x')) {
    normalized = '0x' + normalized;
  }
  if (normalized.length !== 66) {
    throw new Error(`Invalid private key length: ${normalized.length - 2} (expected 64 hex chars)`);
  }
  return normalized;
}

async function createPaymentHeader(
  privateKey: string,
  recipient: string,
  amount: string
): Promise<string> {
  const normalizedKey = normalizePrivateKey(privateKey);
  const account = privateKeyToAccount(normalizedKey as `0x${string}`);
  
  const client = createWalletClient({
    account,
    chain: base,
    transport: http('https://mainnet.base.org'),
  });

  const nonce = `0x${Array.from({ length: 32 }, () => 
    Math.floor(Math.random() * 256).toString(16).padStart(2, '0')
  ).join('')}` as `0x${string}`;
  
  const now = BigInt(Math.floor(Date.now() / 1000));
  const validAfter = now - BigInt(5);
  const validBefore = now + BigInt(3600);

  const authorization = {
    from: account.address,
    to: recipient as Address,
    value: BigInt(amount),
    nonce,
    validAfter,
    validBefore,
  };

  // Sign with EIP-712 (EIP-3009 TransferWithAuthorization per x402 SDK)
  const signature = await client.signTypedData({
    domain: EIP712_DOMAIN,
    types: EIP712_TYPES,
    primaryType: 'TransferWithAuthorization',
    message: authorization,
  });

  const paymentPayload = {
    scheme: 'exact',
    network: HOUSE_JOB_CONFIG.network,
    chainId: HOUSE_JOB_CONFIG.chainId,
    authorization: {
      from: authorization.from,
      to: authorization.to,
      value: authorization.value.toString(),
      nonce: authorization.nonce,
      validAfter: authorization.validAfter.toString(),
      validBefore: authorization.validBefore.toString(),
    },
    signature,
  };

  return `x402 ${Buffer.from(JSON.stringify(paymentPayload)).toString('base64')}`;
}

// =============================================================================
// CRON HANDLER
// =============================================================================

async function houseX402Handler(
  _request: NextRequest,
  context: CronJobContext
): Promise<{ ok: boolean; [key: string]: any }> {
  const { runId, requestId } = context;
  const startTime = Date.now();

  // Environment gating - only run in production
  if (process.env.NODE_ENV !== 'production') {
    logger.info({ runId, requestId, msg: 'Skipping house x402 job - not in production' });
    return {
      ok: true,
      skipped: true,
      reason: 'non-production',
      environment: process.env.NODE_ENV,
    };
  }

  // Validate required environment variables
  const privateKey = process.env.PRIVATE_KEY;
  const recipientAddress = process.env.X402_RECIPIENT_ADDRESS;
  
  // Build endpoint URL - always use production URL
  let appUrl = (process.env.NEXT_PUBLIC_APP_URL || 'https://www.nexflowapp.app').trim();
  while (appUrl.endsWith('/')) {
    appUrl = appUrl.slice(0, -1);
  }
  if (appUrl.includes('nexflowapp.app') && !appUrl.includes('www.')) {
    appUrl = appUrl.replace('://nexflowapp.app', '://www.nexflowapp.app');
  }
  const endpointUrl = `${appUrl}/api/v1/metered/url-enrich`;

  if (!privateKey) {
    logger.error({ runId, requestId, msg: 'House x402 job failed: PRIVATE_KEY not set' });
    return {
      ok: false,
      error: 'PRIVATE_KEY environment variable is required',
      errorCode: 'MISSING_PRIVATE_KEY',
    };
  }

  if (!recipientAddress) {
    logger.error({ runId, requestId, msg: 'House x402 job failed: X402_RECIPIENT_ADDRESS not set' });
    return {
      ok: false,
      error: 'X402_RECIPIENT_ADDRESS environment variable is required',
      errorCode: 'MISSING_RECIPIENT_ADDRESS',
    };
  }

  logger.info({
    runId,
    requestId,
    endpoint: endpointUrl,
    amount: HOUSE_JOB_CONFIG.targetPriceUsdc,
    msg: 'Starting house x402 job',
  });

  try {
    // Step 1: Create payment header
    logger.debug({ runId, requestId, msg: 'Creating payment authorization' });
    const paymentHeader = await createPaymentHeader(
      privateKey,
      recipientAddress,
      HOUSE_JOB_CONFIG.priceAmount
    );

    // Step 2: Call the endpoint with payment
    logger.debug({ runId, requestId, msg: 'Calling endpoint with payment' });
    const response = await fetch(endpointUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'x-payment': paymentHeader,
        'x-trace-id': runId,
        'User-Agent': 'NexFlow-HouseJob/1.0',
        'Accept': 'application/json',
        'X-Vercel-Skip-Toolbar': 'true',
      },
      body: JSON.stringify(HOUSE_JOB_CONFIG.payload),
    });

    const latencyMs = Date.now() - startTime;

    // Parse response
    let responseBody: Record<string, unknown> = {};
    let rawBody = '';
    const contentType = response.headers.get('content-type') || '';
    
    try {
      rawBody = await response.text();
      if (contentType.includes('application/json') && rawBody) {
        responseBody = JSON.parse(rawBody);
      }
    } catch (parseError) {
      logger.warn({ runId, requestId, rawBody, parseError: String(parseError) }, 'Failed to parse response body');
    }

    // Extract x402 headers
    const x402Verified = response.headers.get('X-x402-Verified');
    const x402TxHash = response.headers.get('X-x402-TxHash');
    const x402Facilitator = response.headers.get('X-x402-Facilitator');

    if (response.ok) {
      logger.info({
        runId,
        requestId,
        latencyMs,
        facilitator: x402Facilitator,
        txHash: x402TxHash,
        verified: x402Verified,
        msg: 'House x402 job completed successfully',
      });

      return {
        ok: true,
        job: 'house-x402-job',
        endpoint: endpointUrl,
        status: 'success',
        latencyMs,
        paymentAmountUsdc: HOUSE_JOB_CONFIG.targetPriceUsdc,
        facilitatorId: x402Facilitator || null,
        transactionHash: x402TxHash || null,
      };
    } else {
      const errorCode = (responseBody.code as string) || `HTTP_${response.status}`;
      const errorMessage = (responseBody.error as string) || (responseBody.message as string) || `HTTP ${response.status}`;

      logger.error({
        runId,
        requestId,
        latencyMs,
        statusCode: response.status,
        errorCode,
        errorMessage,
        msg: 'House x402 job failed - endpoint returned error',
      });

      return {
        ok: false,
        job: 'house-x402-job',
        endpoint: endpointUrl,
        status: 'error',
        latencyMs,
        error: errorMessage,
        errorCode,
        httpStatus: response.status,
      };
    }
  } catch (error) {
    const latencyMs = Date.now() - startTime;
    const errorMessage = error instanceof Error ? error.message : 'Unknown error';

    logger.error({
      runId,
      requestId,
      latencyMs,
      error: errorMessage,
      msg: 'House x402 job failed with exception',
    });

    return {
      ok: false,
      job: 'house-x402-job',
      endpoint: endpointUrl,
      status: 'error',
      latencyMs,
      error: errorMessage,
      errorCode: 'EXCEPTION',
    };
  }
}

// =============================================================================
// EXPORTS
// =============================================================================

export const dynamic = 'force-dynamic';
export const maxDuration = 30;

export const GET = withCronJobTracking(houseX402Handler, {
  jobId: 'house-x402-job',
  timeout: 25000, // 25s (leave buffer for wrapper overhead)
  minInterval: 9 * 60 * 1000, // 9 minutes (schedule is every 10 min)
});
